import Observer from './observer'
export function initState(vm){ 
    //做不同的初始化工作 
    let opts = vm.$options;
    if(opts.data){
        initData(vm); // 初始化数据
    }
    if(opts.computed){
        initComputed(); // 初始化计算属性
    }
    if(opts.watch){
        initWatch(); // 初始化watch
    }
}
export function observe(data){
    if(typeof data !== 'object' || data == null) {
        return // 不是对象或者是null 我就不用执行后续逻辑
    }
    return new Observer(data)
}
function proxy(vm, source, key) { //代理数据 vm.msg = vm._data.msg
    Object.defineProperty(vm, key, {
        get() {
            return vm[source][key];
        },
        set(newValue) {
            vm[source][key] = newValue;
        }
    })
}

/**
 * 数据劫持 将传入数据利用Object.defineProperty重新定义
 * @param {*} vm 
 */
function initData(vm) { 
    let data = vm.$options.data; //用户传入的data
    //这种带_, 如_data，一般都是临时变量
    data = vm._data = typeof data === 'function' ? data.call(vm) : data || {}; //判断data是否为function, data() {retrun ...} 或者 data: {}
    //把数据绑到vm上
    for(let key in data) {
        proxy(vm, '_data', key); //会将对vm上的取值操作和赋值操作代理给vm._data 属性
    }
    observe(data); //观察数据，数据劫持
}

